import React from 'react';

class Home extends React.Component {
    render() {
        return (
            <div className="col-10 mx-auto col-md-8 mt-4 content">
                <h5 className="todo-title text-center">"Personal assistant" APP</h5>
                <p className="text-center">autor: Marcin Krupa</p>
                <h6 className="text-center"><strong>Wykorzystano: JavaScript, React.js, react-router, 
                    HTML, CSS, Bootstrap, RWD, JSON, npm</strong></h6>
                    <br/>
                <h6 className="text-center"><strong>Opis aplikacji</strong></h6>
                <p>Aplikacja Personal Assistant App to nowoczesny notatnik online. Pozwala uytkownikowi 
                   na pen kontrol nad jego prywatnymi sprawami. Mowa tutaj o:</p>
                <ul>
                    <li>Ratach, kredytach, comiesicznych rachunkach - lista patnoci</li>
                    <li>Rzeczach o ktrych nie moemy zapomnie: spotkanie, konferencja, webinar - lista zada</li>
                </ul>
                <p>Dodatkow funkcj jest kalkulator, na ktrym moemy szybko podliczy nasze zalegoci - lista zalegoci.</p>
                <h6 className="text-center"><strong>Korzystanie z aplikacji</strong></h6>
                <p>Funkcjonowanie i obsuga aplikacji s bardzo proste. W "licie patnoci" wprowadzamy dwie dane: 
                    nazw i kwot patnoci. Dodajemy patno do listy za pomoc buttona "Dodaj patno". Po uzupenieniu
                    wasnej listy patnoci, otrzymujemy kompletny wykaz naszych wszystkich opat.
                </p>
                <p>W "licie zada" wprowadzamy wszystkie rzeczy do zrobienia, dzisiaj, jutro czy do koca miesica.
                    Po wpisaniu nazwy zadania, dodajemy element do listy za pomoc buttona "Dodaj zadanie". 
                    Element z listy po dodaniu moemy rwnie edytowa lub usuwa. 
                    Obydwie akcje za pomoc ikonek: edit i trash. 
                </p>
            </div>           
        )
    }
}

export default Home;


